import React from "react";

const Album = ({title, preview, amount, albumid, onClick}) => {
  return (
    <a className="album" albumid={albumid} onClick={onClick}>
      <img src={`${preview}`} alt="album preview photo"/>
      <p className="albumTitle">{title}</p>
      <p className="amountOfPhotos">{`${amount} photos`}</p>
    </a>
  );
}

export {Album};
