import React from "react";

const User = ({name, userid, onClick}) => {
  let nameParts = name.split(" ");
  return (
    <a className="user" userid={userid} onClick={onClick}>
      <img src="./userpic.png" alt="default user avatar"/>
      <p className="userFirstName">{nameParts[0]}</p>
      <p className="userLastName">{nameParts[1]}</p>
    </a>
  );
}

export {User};
