import React, {Component} from "react";

import {Navbar} from "./Navbar.js";
import {User} from "./User.js";
import {Album} from "./Album.js";
import {Photo} from "./Photo.js";
import {Popup} from "./Popup.js";

class MainWindow extends Component {
  state = {
    currentView: "users",
    users: [],
    albums: [],
    photos: [],
    currentUserId: "",
    currentAlbumId: "",
    currentPhotoId: "",
    error: null,
    requiresUpdate: true,
  };

  _isMounted = false;

  componentDidMount = () => {
    this._isMounted = true;
  }

  componentWillUnmount = () => {
    this._isMounted = false;
  }

  getUsers = () => {
    let {users} = this.state;
    let url = "https://jsonplaceholder.typicode.com/users/";
    fetch(url, {
        method: 'GET',
    })
      .then(result => {
        if (this._isMounted) {
          result.json()
          .then(data => {
            if (users.length !== data.length) {
              this.setState({users: data});
            } else {
              for (let i = 0; i < data.length; i++) {
                if (users[i].email !== data[i].email) {
                  this.setState({users: data});
                }
              }
            }
          });
        }
      })
      .catch(error => this.setState({ error }));
  }

  showUsers = () => {
    let {users} = this.state;
    return (
      <>
        {users.map((user, index) =>
          <User
            key={index}
            userid={user.id}
            name={user.name}
            onClick={this.onUserClick}
          >
          </User>
        )}
      </>
    );
  }

  getAlbums = () => {
    let {currentUserId} = this.state;
    if (this._isMounted) {
      let url = "https://jsonplaceholder.typicode.com/albums";
      fetch(`${url}?userId=${currentUserId}`, {
          method: 'GET',
      })
        .then(result => {
            if (result.ok) {
              result.json()
              .then(data => {
                let temp = [];
                temp.length = data.length;
                data.forEach((album, index) => {
                  let preview = this.getAlbumPreview(album.id);
                  preview.then(resolveData => {
                    album.photo = resolveData.thumbnailUrl;
                    album.amountOfPhotos = resolveData.amountOfPhotos;
                    temp[index] = album;
                    this.setState({albums: temp, requiresUpdate: false});
                  })
                });
              });
            } else {
              return false;
            }
        })
        .catch(error => {this.setState({ error })});
    }
  }

  getAlbumPreview = (albumId) => {
    return new Promise(resolve => {
      let url = "https://jsonplaceholder.typicode.com/photos";
      fetch(`${url}?albumId=${albumId}`, {
          method: 'GET',
      })
        .then(result => {
          if (this._isMounted) {
            if (result.ok) {
              result.json()
              .then(data => {
                let resolveData = {};
                resolveData.thumbnailUrl = data[0].thumbnailUrl;
                resolveData.amountOfPhotos = data.length;
                resolve(resolveData);
              });
            } else {
              return false;
            }
          } else {
            resolve("");
          }
        })
        .catch(error => {this.setState({ error })});
    });
  }



  showAlbums = () => {
    let {albums} = this.state;
    return (
      <>
        {albums.map((album, index) =>
          <Album
            key={index}
            title={album.title}
            preview={album.photo}
            amount={album.amountOfPhotos}
            albumid={album.id}
            onClick={this.onAlbumClick}
          >
          </Album>
        )}
      </>
    );
  }

  getPhotos = () => {
    let {currentAlbumId} = this.state;
    if (this._isMounted) {
      let url = "https://jsonplaceholder.typicode.com/photos";
      fetch(`${url}?albumId=${currentAlbumId}`, {
          method: 'GET',
      })
        .then(result => {
            if (result.ok) {
              result.json()
              .then(data => {
                let temp = [];
                temp.length = data.length;
                data.forEach((photo, index) => {
                  temp[index] = photo;
                  this.setState({photos: temp, requiresUpdate: false});
                });
              });
            } else {
              return false;
            }
        })
        .catch(error => {this.setState({ error })});
    }
  }

  showPhotos = () => {
    let {photos} = this.state;
    return (
      <>
        {photos.map((photo, index) =>
          <Photo
            key={index}
            title={photo.title}
            url={photo.url}
            photoid={index}
            onClick={this.onPhotoClick}
          >
          </Photo>
        )}
      </>
    );
  }

  showPopups = () => {
    let {currentPhotoId} = this.state;
    let url = this.state.photos[currentPhotoId].url;
    return (
        <Popup
          url={url}
          onClick={this.onPopupClick}
        >
        </Popup>
    );
  }

  onNavbarClick = (event) => {
    let prev = event.currentTarget.getAttribute("prevwindow");
    this.setState({requiresUpdate: false, currentView: prev});
  }

  onUserClick = (event) => {
    let currentUserId = parseInt(event.currentTarget.getAttribute("userid"));
    if (currentUserId === this.state.currentUserId) {
      this.setState({requiresUpdate: false, currentUserId, currentView: "albums"});
    } else {
      this.setState({requiresUpdate: true, currentUserId, albums: [], currentView: "albums"});
    }
  }

  onAlbumClick = (event) => {
    let currentAlbumId = parseInt(event.currentTarget.getAttribute("albumid"));
    if (currentAlbumId === this.state.currentAlbumId) {
      this.setState({requiresUpdate: false, currentAlbumId, currentView: "photos"});
    } else {
      this.setState({requiresUpdate: true, currentAlbumId, photos: [], currentView: "photos"});
    }
  }

  onPhotoClick = (event) => {
    let currentPhotoId = parseInt(event.currentTarget.getAttribute("photoid"));
    this.setState({requiresUpdate: false, currentPhotoId, currentView: "popups"});
  }

  onPopupLeftClick = () => {
    let {currentPhotoId} = this.state;
    currentPhotoId = (currentPhotoId-1)%this.state.photos.length;
    this.setState({currentPhotoId});
  }

  onPopupRightClick = () => {
    let {currentPhotoId} = this.state;
    currentPhotoId = (currentPhotoId+1)%this.state.photos.length;
    this.setState({currentPhotoId});
  }

  onPopupEmptyClick = () => {
    this.setState({currentView: "photos"});
  }

  onPopupClick = (event) => {
    let popupLeft = event.currentTarget.querySelector("#popupLeft");
    let popupRight = event.currentTarget.querySelector("#popupRight");
    let img = event.currentTarget.querySelector("img");
    if (popupLeft.contains(event.target)) {
      let {currentPhotoId} = this.state;
      currentPhotoId = (currentPhotoId-1+this.state.photos.length)%this.state.photos.length;
      this.setState({currentPhotoId});
    } else if (popupRight.contains(event.target)) {
      let {currentPhotoId} = this.state;
      currentPhotoId = (currentPhotoId+1+this.state.photos.length)%this.state.photos.length;
      this.setState({currentPhotoId});
    } else if (img.contains(event.target)) {
    } else {
      this.setState({currentView: "photos"});
    }
  }

  render() {
    let {
      currentView,
      error,
      requiresUpdate
    } = this.state;
    let prevWindow = (currentView === "users") ? "" :
    (currentView === "albums") ? "Users" : "Albums";

    let content;
    let popup;
    if (currentView === "users") {
      if (requiresUpdate) {
        this.getUsers();
      }
      content = this.showUsers();
    } else if (currentView === "albums") {
      if (requiresUpdate) {
        this.getAlbums();
      }
      content = this.showAlbums();
    } else {
      if (requiresUpdate) {
        this.getPhotos();
      }
      content = this.showPhotos();
      if (currentView === "popups") {
        popup = this.showPopups();
      }
    }
    if (this._isMounted) {
        let base = document.querySelector("main #content");
        let compWidth = window.getComputedStyle(base).width;
        let childWidth;
        if (window.screen.availWidth < 900) {
          childWidth = 130;
        } else {
          childWidth = 200;
        }
        base.style.padding = `0px ${(parseInt(compWidth)%childWidth)/2}px`;
    }
    if (error) {
      return <p>Something went wrong.</p>;
    }
    return (
        <main>
          <Navbar
            prevWindow = {prevWindow}
            onClick = {this.onNavbarClick}
          >
            {currentView !== "popups" ?
              currentView.toUpperCase() :
              "PHOTOS"
            }
          </Navbar>
          <div id="content">
            {
              (() => content)()
            }
          </div>
          {currentView === "popups" ?
            (() => popup)(): true}
        </main>
    );
  }
}

export {MainWindow};
