import React from "react";

const Navbar = ({prevWindow="", children, onClick}) => {
  return (
    <div id="navbar">
      <>
        {prevWindow != "" ?
          <a id="navbarBack" onClick={onClick} prevwindow={prevWindow.toLowerCase()}>&larr; {`To ${prevWindow}`}</a> : true
        }
      </>
      <div id="navbarTitle">{children}</div>
    </div>
  );
}

export {Navbar};
